
.monster-detail-view {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #fafafa;
}

// ====================  ====================
.detail-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  background: #fff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.08);

  .back-btn,
  .close-btn {
    padding: 10px 18px;
    background: #fff;
    border: 2px solid rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: '', serif;
    font-size: 14px;
    font-weight: 600;
    color: #555;

    &:hover {
      background: #f5f5f5;
      border-color: #8b4513;
      color: #8b4513;
    }

    i {
      font-size: 14px;
    }
  }

  .back-btn:hover {
    transform: translateX(-3px);
  }

  .close-btn:hover {
    transform: rotate(90deg);
  }
}

// ====================  ====================
.detail-content {
  flex: 1;
  overflow-y: scroll; // 
  overflow-x: hidden;
  padding: 25px 30px 20px 30px; //  padding
  min-height: 0; //  flex 

  // 
  will-change: scroll-position;
  -webkit-overflow-scrolling: touch;

  //  - 
  &::-webkit-scrollbar {
    width: 12px; // 
  }

  &::-webkit-scrollbar-track {
    background: #e8e8e8; // 
    border-radius: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background: #888; // 
    border-radius: 6px;
    border: 2px solid #e8e8e8; // 

    &:hover {
      background: #555; // hover 
    }
  }

  //  Firefox 
  scrollbar-width: auto; // 
  scrollbar-color: #888 #e8e8e8;
}

// ====================  ====================
.monster-header {
  display: flex;
  gap: 30px;
  margin-bottom: 30px;
  padding: 25px;
  background: #fff;
  border-radius: 12px;
  border: 2px solid rgba(0, 0, 0, 0.08);

  .monster-avatar-large {
    width: 200px;
    height: 200px;
    border-radius: 12px;
    overflow: hidden;
    background: #f0f0f0;
    border: 3px solid rgba(0, 0, 0, 0.1);
    flex-shrink: 0;
    position: relative;

    .avatar-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .image-source {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      padding: 5px;
      background: rgba(0, 0, 0, 0.7);
      color: #fff;
      font-size: 10px;
      text-align: center;

      small {
        display: block;
      }
    }
  }

  .monster-basic-info {
    flex: 1;

    .monster-name {
      margin: 0 0 15px 0;
      font-size: 28px;
      font-weight: 700;
      color: #2c2c2c;
      font-family: '', serif;
      line-height: 1.3;

      .monster-english {
        display: block;
        font-size: 18px;
        font-weight: 400;
        color: #888;
        margin-top: 5px;
      }
    }

    .monster-quick-stats {
      display: flex;
      flex-wrap: wrap;
      gap: 15px;
      margin-bottom: 20px;

      .quick-stat {
        display: flex;
        align-items: center;
        gap: 6px;
        padding: 8px 15px;
        background: #f8f5f0;
        border-radius: 6px;
        font-family: '', serif;
        font-size: 14px;
        font-weight: 600;
        color: #2c2c2c;
        border: 1px solid rgba(139, 69, 19, 0.2);

        i {
          color: #8b4513;
          font-size: 12px;
        }
      }
    }

    .action-buttons {
      display: flex;
      gap: 12px;

      .action-btn {
        padding: 12px 20px;
        border: 2px solid #8b4513;
        border-radius: 6px;
        cursor: pointer;
        transition: all 0.2s;
        display: flex;
        align-items: center;
        gap: 8px;
        font-family: '', serif;
        font-size: 14px;
        font-weight: 600;

        i {
          font-size: 14px;
        }

        &.image-btn {
          background: #fff;
          color: #8b4513;

          &:hover {
            background: #8b4513;
            color: #fff;
          }
        }

        &.copy-btn {
          background: #8b4513;
          color: #fff;

          &:hover {
            background: #6d3610;
            transform: translateY(-2px);
            box-shadow: 0 4px 12px rgba(139, 69, 19, 0.3);
          }
        }
      }
    }
  }
}

// ====================  ====================
.tabs-container {
  background: #fff;
  border-radius: 12px;
  border: 2px solid rgba(0, 0, 0, 0.08);
  margin-bottom: 30px;
  overflow: hidden;

  .tabs-header {
    display: flex;
    background: #f8f5f0;
    border-bottom: 2px solid rgba(0, 0, 0, 0.08);
    overflow-x: auto;

    &::-webkit-scrollbar {
      height: 4px;
    }

    &::-webkit-scrollbar-thumb {
      background: #ccc;
      border-radius: 2px;
    }

    .tab-btn {
      flex: 1;
      min-width: 120px;
      padding: 15px 20px;
      background: transparent;
      border: none;
      border-bottom: 3px solid transparent;
      cursor: pointer;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 8px;
      font-family: '', serif;
      font-size: 15px;
      font-weight: 600;
      color: #666;

      i {
        font-size: 14px;
      }

      &:hover {
        background: rgba(139, 69, 19, 0.05);
        color: #8b4513;
      }

      &.active {
        background: #fff;
        color: #8b4513;
        border-bottom-color: #8b4513;
      }
    }
  }

  .tab-content {
    .tab-pane {
      padding: 25px;
    }
  }
}

// 
.attributes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 15px;
  margin-bottom: 25px;

  .attr-item {
    display: flex;
    align-items: flex-start; // 
    padding: 12px 15px;
    background: #f8f5f0;
    border-radius: 6px;
    font-family: '', serif;
    font-size: 14px;
    border: 1px solid rgba(0, 0, 0, 0.05);

    .attr-label {
      font-weight: 600;
      color: #555;
      margin-right: 8px;
      min-width: 90px;
      flex-shrink: 0; // 
    }

    .attr-value {
      flex: 1;
      color: #2c2c2c;
      word-break: break-word; // 
      line-height: 1.5; // 
    }
  }
}

// 
.combat-stats {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 25px;

  .combat-item {
    display: flex;
    align-items: center;
    padding: 15px;
    background: #f8f5f0;
    border-radius: 6px;
    font-family: '', serif;
    font-size: 14px;
    border: 1px solid rgba(139, 69, 19, 0.1);

    .combat-label {
      display: flex;
      align-items: center;
      gap: 8px;
      font-weight: 600;
      color: #555;
      min-width: 140px;

      i {
        color: #8b4513;
        font-size: 14px;
      }
    }

    .combat-value {
      flex: 1;
      color: #2c2c2c;
      font-weight: 500;
    }
  }
}

// 
.description-section {
  margin-bottom: 25px;

  .section-title {
    margin: 0 0 15px 0;
    font-size: 18px;
    font-weight: 600;
    color: #2c2c2c;
    font-family: '', serif;
    display: flex;
    align-items: center;
    gap: 10px;
    padding-bottom: 10px;
    border-bottom: 2px solid rgba(0, 0, 0, 0.08);

    i {
      color: #8b4513;
    }
  }

  .description-text {
    font-family: '', serif;
    font-size: 15px;
    line-height: 1.8;
    color: #444;
    margin: 0;
    text-align: justify;
    white-space: pre-wrap;
  }
}

// 
.special-rules {
  margin-top: 25px;

  .rule-item {
    padding: 15px;
    background: #fffbf5;
    border-left: 4px solid #8b4513;
    border-radius: 6px;
    margin-bottom: 15px;

    .rule-name {
      margin: 0 0 10px 0;
      font-size: 16px;
      font-weight: 600;
      color: #8b4513;
      font-family: '', serif;
    }

    .rule-content {
      margin: 0;
      font-family: '', serif;
      font-size: 14px;
      line-height: 1.7;
      color: #444;
      white-space: pre-wrap;
    }
  }
}

// 
.notes-editor {
  .note-field {
    margin-bottom: 20px;

    .note-label {
      display: flex;
      align-items: center;
      gap: 8px;
      margin-bottom: 8px;
      font-family: '', serif;
      font-size: 14px;
      font-weight: 600;
      color: #555;

      i {
        color: #8b4513;
      }
    }

    .note-input,
    .note-textarea {
      width: 100%;
      padding: 12px 15px;
      border: 2px solid rgba(0, 0, 0, 0.1);
      border-radius: 6px;
      font-family: '', serif;
      font-size: 14px;
      background: #fff;
      transition: all 0.2s;

      &::placeholder {
        color: #bbb;
      }

      &:focus {
        outline: none;
        border-color: #8b4513;
        box-shadow: 0 0 0 3px rgba(139, 69, 19, 0.08);
      }
    }

    .note-textarea {
      resize: vertical;
      line-height: 1.6;
    }
  }

  .save-notes-btn {
    width: 100%;
    padding: 15px;
    margin-bottom: 30px; // 
    background: #8b4513;
    color: #fff;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-family: '', serif;
    font-size: 16px;
    font-weight: 600;

    &:hover {
      background: #6d3610;
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba(139, 69, 19, 0.3);
    }

    i {
      font-size: 16px;
    }
  }
}

// ====================  ====================
.encounter-section {
  padding: 25px;
  margin-bottom: 30px; // 
  background: #fff;
  border-radius: 12px;
  border: 2px solid rgba(0, 0, 0, 0.08);

  .section-title {
    margin: 0 0 20px 0;
    font-size: 18px;
    font-weight: 600;
    color: #2c2c2c;
    font-family: '', serif;
    display: flex;
    align-items: center;
    gap: 10px;

    i {
      color: #f39c12;
    }
  }

  .encounter-info {
    margin-bottom: 20px;

    .encounter-stat {
      display: flex;
      align-items: center;
      gap: 10px;
      padding: 12px 15px;
      background: #f8f5f0;
      border-radius: 6px;
      margin-bottom: 10px;
      font-family: '', serif;
      font-size: 14px;
      color: #555;
      word-break: break-word; // 
      flex-wrap: wrap; // 

      i {
        color: #8b4513;
        font-size: 14px;
        flex-shrink: 0; // 
      }

      strong {
        color: #2c2c2c;
        font-weight: 700;
      }
    }

    .encounter-history {
      margin-top: 15px;

      .history-title {
        margin: 0 0 10px 0;
        font-size: 14px;
        font-weight: 600;
        color: #555;
        font-family: '', serif;
      }

      .history-entry {
        display: flex;
        align-items: flex-start; // 
        gap: 10px;
        padding: 10px;
        background: #fafafa;
        border-radius: 4px;
        margin-bottom: 6px;
        font-size: 13px;
        font-family: '', serif;
        flex-wrap: wrap; // 
        position: relative; // 

        .entry-badge {
          padding: 3px 8px;
          border-radius: 4px;
          font-size: 11px;
          font-weight: 600;
          color: #fff;
          flex-shrink: 0; // 
          white-space: nowrap; // 

          &.auto {
            background: #3498db;
          }

          &.manual {
            background: #2ecc71;
          }
        }

        .entry-time {
          color: #888;
          flex-shrink: 0; // 
          white-space: nowrap; // 
        }

        .entry-context {
          flex: 1;
          min-width: 0; // 
          color: #666;
          font-size: 12px;
          font-style: italic;
          word-break: break-word; // 
          line-height: 1.5; // 
        }

        .delete-entry-btn {
          padding: 4px 8px;
          background: transparent;
          border: 1px solid #e74c3c;
          color: #e74c3c;
          border-radius: 4px;
          cursor: pointer;
          transition: all 0.2s;
          font-size: 11px;
          flex-shrink: 0;
          margin-left: auto; // 

          &:hover {
            background: #e74c3c;
            color: #fff;
            transform: scale(1.05);
          }

          i {
            font-size: 10px;
          }
        }
      }
    }
  }

  .no-encounter {
    padding: 30px;
    text-align: center;
    color: #999;
    font-family: '', serif;
    font-size: 15px;

    i {
      font-size: 40px;
      margin-bottom: 10px;
      display: block;
    }
  }

  .encounter-btn {
    width: 100%;
    padding: 12px;
    background: #fff;
    color: #f39c12;
    border: 2px solid #f39c12;
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    font-family: '', serif;
    font-size: 14px;
    font-weight: 600;

    &:hover {
      background: #f39c12;
      color: #fff;
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba(243, 156, 18, 0.3);
    }

    i {
      font-size: 14px;
    }
  }
}

// ====================  ====================
.bottom-spacer {
  height: 200px; // 
  flex-shrink: 0; // 
}

// ====================  ====================
@media (max-width: 768px) {
  .detail-content {
    padding: 20px;
  }

  .monster-header {
    flex-direction: column;
    padding: 20px;

    .monster-avatar-large {
      width: 100%;
      height: 250px;
    }

    .monster-basic-info {
      .action-buttons {
        flex-direction: column;

        .action-btn {
          width: 100%;
          justify-content: center;
        }
      }
    }
  }

  .attributes-grid {
    grid-template-columns: 1fr;
  }

  .tabs-header {
    .tab-btn {
      min-width: 100px;
      padding: 12px 15px;
      font-size: 13px;
    }
  }

  .tab-content .tab-pane {
    padding: 20px;
  }
}
