
.monster-list-view {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: #fafafa;
}

// ====================  ====================
.list-header {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 20px 30px;
  background: #fff;
  border-bottom: 2px solid rgba(0, 0, 0, 0.08);

  .back-btn {
    padding: 10px 18px;
    background: #fff;
    border: 2px solid rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 8px;
    font-family: '', serif;
    font-size: 14px;
    font-weight: 600;
    color: #555;

    &:hover {
      background: #f5f5f5;
      border-color: #8b4513;
      color: #8b4513;
      transform: translateX(-3px);
    }

    i {
      font-size: 14px;
    }
  }

  .list-title {
    flex: 1;
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    color: #2c2c2c;
    font-family: '', serif;
  }
}

// ====================  ====================
.search-bar {
  position: relative;
  padding: 20px 30px;
  background: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);

  .search-icon {
    position: absolute;
    left: 45px;
    top: 50%;
    transform: translateY(-50%);
    color: #999;
    font-size: 14px;
  }

  .search-input {
    width: 100%;
    padding: 10px 15px 10px 40px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    font-family: '', serif;
    font-size: 14px;
    background: #fafafa;
    transition: all 0.2s;

    &::placeholder {
      color: #bbb;
    }

    &:focus {
      outline: none;
      border-color: #8b4513;
      background: #fff;
      box-shadow: 0 0 0 3px rgba(139, 69, 19, 0.08);
    }
  }
}

// ====================  ====================
.monster-list {
  flex: 1;
  overflow-y: scroll; // 
  overflow-x: hidden;
  padding: 20px 30px 20px 30px; //  padding
  min-height: 0; //  flex 

  // 
  will-change: scroll-position;
  -webkit-overflow-scrolling: touch;

  //  - 
  &::-webkit-scrollbar {
    width: 12px; // 
  }

  &::-webkit-scrollbar-track {
    background: #e8e8e8; // 
    border-radius: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background: #888; // 
    border-radius: 6px;
    border: 2px solid #e8e8e8; // 

    &:hover {
      background: #555; // hover 
    }
  }

  //  Firefox 
  scrollbar-width: auto; //  thin
  scrollbar-color: #888 #e8e8e8;
}

// 
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  color: #999;

  .empty-icon {
    font-size: 80px;
    opacity: 0.3;
    margin-bottom: 20px;
  }

  .empty-text {
    font-family: '', serif;
    font-size: 16px;
    margin: 0;
  }
}

// 
.monster-card {
  display: flex;
  align-items: center;
  gap: 20px;
  padding: 20px;
  background: #fff;
  border: 2px solid rgba(0, 0, 0, 0.08);
  border-radius: 10px;
  margin-bottom: 15px;
  cursor: pointer;
  transition: all 0.2s;

  // 
  width: 100%;
  box-sizing: border-box;

  // 
  will-change: transform;
  transform: translateZ(0); // 

  &:hover {
    border-color: #8b4513;
    background: #f8f5f0;
    transform: translateY(-3px) translateZ(0);
    box-shadow: 0 6px 20px rgba(139, 69, 19, 0.15);

    .detail-btn {
      background: #8b4513;
      color: #fff;
    }
  }

  &:active {
    transform: translateY(-1px) translateZ(0);
  }

  .monster-avatar {
    width: 80px;
    height: 80px;
    border-radius: 8px;
    overflow: hidden;
    background: #f0f0f0;
    border: 2px solid rgba(0, 0, 0, 0.08);
    flex-shrink: 0;

    .avatar-img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .monster-info {
    flex: 1;
    min-width: 0;
    overflow: hidden; // 

    .monster-name {
      margin: 0 0 8px 0;
      font-size: 18px;
      font-weight: 600;
      color: #2c2c2c;
      font-family: '', serif;
      display: flex;
      align-items: center;
      gap: 8px;
      flex-wrap: wrap;
      word-break: break-word; // 

      .monster-english {
        font-size: 14px;
        font-weight: 400;
        color: #888;
        word-break: break-word;
      }
    }

    .monster-stats {
      display: flex;
      align-items: center;
      gap: 10px;
      flex-wrap: wrap;
      font-size: 14px;
      color: #666;
      font-family: '', serif;
      margin-bottom: 8px;

      .stat-item {
        display: flex;
        align-items: center;
        gap: 5px;
        white-space: nowrap; // 

        i {
          color: #8b4513;
          font-size: 12px;
        }
      }

      .stat-divider {
        color: #ddd;
      }
    }

    .encounter-badge {
      display: inline-flex;
      align-items: center;
      gap: 5px;
      padding: 4px 10px;
      background: linear-gradient(135deg, #f39c12, #e67e22);
      color: #fff;
      border-radius: 12px;
      font-size: 12px;
      font-weight: 600;
      font-family: '', serif;
      box-shadow: 0 2px 6px rgba(243, 156, 18, 0.3);
      white-space: nowrap; // 

      i {
        font-size: 10px;
      }
    }
  }

  .card-action {
    flex-shrink: 0;
    display: flex;
    align-items: center;

    .detail-btn {
      padding: 10px 16px; //  padding
      background: #fff;
      border: 2px solid #8b4513;
      border-radius: 6px;
      cursor: pointer;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      gap: 6px; // 
      font-family: '', serif;
      font-size: 13px; // 
      font-weight: 600;
      color: #8b4513;
      white-space: nowrap; // 

      i {
        font-size: 12px;
        flex-shrink: 0; // 
      }

      span {
        flex-shrink: 0; // 
      }
    }
  }
}

// ====================  ====================
.bottom-spacer {
  height: 200px; // 
  flex-shrink: 0; // 
}

// ====================  ====================
@media (max-width: 768px) {
  .list-header {
    padding: 15px 20px;

    .list-title {
      font-size: 18px;
    }
  }

  .search-bar,
  .monster-list {
    padding: 15px 20px;
  }

  .monster-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    padding: 15px;

    .monster-avatar {
      width: 100%;
      height: 150px;
    }

    .card-action {
      width: 100%;

      .detail-btn {
        width: 100%;
        justify-content: center;
      }
    }
  }
}
