
.npc-detail-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 20px;
  overflow-y: auto;
  background-color: #fff;
}

.back-button {
  align-self: flex-start;
  padding: 8px 16px;
  border: 2px solid #000;
  background-color: #fff;
  font-family: '', serif;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  margin-bottom: 20px;
  transition: all 0.2s;

  i {
    margin-right: 8px;
  }

  &:hover {
    background-color: #000;
    color: #fff;
  }
}

.npc-header {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 3px solid #000;
}

.npc-avatar-section {
  flex-shrink: 0;
  text-align: center;
}

.npc-avatar-large {
  width: 150px;
  height: 150px;
  border: 3px solid #000;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .avatar-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    transition: opacity 0.3s;

    i {
      color: #fff;
      font-size: 32px;
    }
  }

  &:hover .avatar-overlay {
    opacity: 1;
  }
}

.avatar-credit {
  font-size: 10px;
  color: #666;
  margin-top: 5px;
}

.npc-header-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.npc-name-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.npc-name {
  font-family: '', serif;
  font-size: 28px;
  font-weight: bold;
  margin: 0;
  letter-spacing: 1px;

  &.name-male {
    color: #4682b4;
  }

  &.name-female {
    color: #ff69b4;
  }
}

.favorite-button {
  width: 40px;
  height: 40px;
  border: 2px solid #000;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;

  &:hover {
    background-color: #ffd700;
    border-color: #ffd700;
  }

  &.active {
    background-color: #ffd700;
    color: #000;
  }
}

.npc-meta-info {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  font-size: 14px;
  color: #666;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 5px;

  i {
    color: #000;
  }
}

.relationship-section {
  margin-top: 5px;
}

.relationship-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  font-size: 14px;
  font-weight: bold;
}

.attitude-badge {
  padding: 2px 10px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;

  &.attitude-hostile {
    background-color: #ff4444;
    color: #fff;
  }
  &.attitude-unfriendly {
    background-color: #ff9944;
    color: #fff;
  }
  &.attitude-neutral {
    background-color: #999;
    color: #fff;
  }
  &.attitude-friendly {
    background-color: #44ff44;
    color: #000;
  }
  &.attitude-helpful {
    background-color: #4444ff;
    color: #fff;
  }
}

.relationship-bar {
  height: 20px;
  background-color: #ddd;
  border: 2px solid #000;
  position: relative;
  overflow: hidden;
}

.relationship-fill {
  height: 100%;
  background: linear-gradient(90deg, #ff4444 0%, #ffff44 50%, #44ff44 100%);
  transition: width 0.3s;
}

.status-badge {
  padding: 8px 12px;
  background-color: #ffe4b5;
  border: 2px solid #000;
  font-size: 14px;
  display: inline-flex;
  align-items: center;
  gap: 8px;

  i {
    color: #ff8c00;
  }
}

.npc-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  padding: 4px 10px;
  background-color: #e0e0e0;
  border: 1px solid #000;
  border-radius: 12px;
  font-size: 12px;
  font-weight: bold;
}

.tab-navigation {
  display: flex;
  border-bottom: 3px solid #000;
  margin-bottom: 20px;
}

.tab-button {
  flex: 1;
  padding: 12px;
  border: none;
  background-color: #f5f5f5;
  border-right: 1px solid #000;
  font-family: '', serif;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;

  &:last-child {
    border-right: none;
  }

  i {
    margin-right: 6px;
  }

  &.active {
    background-color: #fff;
    border-bottom: 3px solid #fff;
    margin-bottom: -3px;
  }

  &:hover:not(.active) {
    background-color: #e0e0e0;
  }
}

.tab-content {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 20px;
}

.tab-pane {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.info-section {
  h3 {
    font-family: '', serif;
    font-size: 18px;
    font-weight: bold;
    margin: 0 0 15px 0;
    padding-bottom: 10px;
    border-bottom: 2px solid #000;

    i {
      margin-right: 8px;
    }
  }
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 15px;
}

.info-item {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #000;

  .label {
    font-weight: bold;
  }

  .value {
    font-family: '', serif;
  }
}

.special-abilities,
.description-text {
  line-height: 1.8;
  font-size: 14px;

  p {
    margin: 8px 0;
  }
}

.equipment-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.equipment-item {
  padding: 10px;
  background-color: #f5f5f5;
  border: 1px solid #000;
  display: flex;
  align-items: center;
  gap: 10px;

  i {
    font-size: 20px;
    color: #666;
  }
}

.inventory-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 12px;
}

.inventory-item {
  padding: 12px;
  background-color: #f5f5f5;
  border: 2px solid #000;
  text-align: center;

  .item-name {
    font-weight: bold;
    margin-bottom: 5px;
  }

  .item-quantity {
    color: #666;
    font-size: 12px;
    margin-bottom: 5px;
  }

  .item-description {
    font-size: 11px;
    color: #666;
    margin-top: 5px;
    border-top: 1px solid #ccc;
    padding-top: 5px;
  }
}

.notes-textarea {
  width: 100%;
  min-height: 150px;
  padding: 12px;
  border: 2px solid #000;
  font-family: '', serif;
  font-size: 14px;
  resize: vertical;
  line-height: 1.6;

  &:focus {
    outline: none;
    border-color: #4682b4;
  }
}

.stats-grid {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 12px;
  background-color: #f5f5f5;
  border: 1px solid #000;

  i {
    font-size: 24px;
    color: #666;
  }

  div {
    strong {
      display: block;
      margin-bottom: 3px;
    }

    span {
      color: #666;
      font-size: 14px;
    }
  }
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: #999;

  i {
    font-size: 48px;
    margin-bottom: 15px;
  }

  p {
    font-size: 16px;
  }
}

.action-buttons {
  display: flex;
  gap: 10px;
  padding-top: 20px;
  border-top: 3px solid #000;
  flex-wrap: wrap;
}

.action-btn {
  flex: 1;
  min-width: 100px;
  padding: 12px 16px;
  border: 2px solid #000;
  background-color: #fff;
  font-family: '', serif;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }

  &.interact-btn:hover {
    background-color: #4682b4;
    border-color: #4682b4;
    color: #fff;
  }

  &.delete-btn {
    border-color: #dc3545;
    color: #dc3545;

    &:hover {
      background-color: #dc3545;
      color: #fff;
    }
  }
}

// 
@media (max-width: 992px) {
  .npc-detail-panel {
    padding: 15px;
  }

  .back-button {
    padding: 8px 14px;
    font-size: 13px;
    margin-bottom: 15px;
    min-height: 44px; // 
  }

  .npc-header {
    flex-direction: column;
    gap: 15px;
    margin-bottom: 15px;
    padding-bottom: 15px;
    border-bottom-width: 2px;
  }

  .npc-avatar-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .npc-avatar-large {
    width: 120px;
    height: 120px;
    border-width: 2px;

    .avatar-overlay i {
      font-size: 24px;
    }
  }

  .avatar-credit {
    font-size: 9px;
  }

  .npc-header-info {
    gap: 10px;
    width: 100%;
  }

  .npc-name-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
  }

  .npc-name {
    font-size: 22px;
  }

  .favorite-button {
    width: 36px;
    height: 36px;
    font-size: 16px;
    position: absolute;
    top: 15px;
    right: 15px;
  }

  .npc-meta-info {
    gap: 10px;
    font-size: 13px;
  }

  .meta-item {
    gap: 4px;
    font-size: 13px;
  }

  .relationship-label {
    font-size: 13px;
  }

  .attitude-badge {
    padding: 2px 8px;
    font-size: 11px;
    border-radius: 10px;
  }

  .relationship-bar {
    height: 18px;
    border-width: 2px;
  }

  .status-badge {
    padding: 6px 10px;
    font-size: 13px;
    border-width: 2px;
  }

  .tag {
    padding: 3px 8px;
    font-size: 11px;
    border-radius: 10px;
  }

  .tab-navigation {
    border-bottom-width: 2px;
    margin-bottom: 15px;
    flex-wrap: wrap;
  }

  .tab-button {
    flex: 1 1 calc(50% - 0.5px);
    padding: 10px 8px;
    font-size: 13px;
    min-height: 44px; // 

    i {
      margin-right: 5px;
    }

    &.active {
      border-bottom-width: 2px;
      margin-bottom: -2px;
    }
  }

  .tab-content {
    margin-bottom: 15px;
  }

  .tab-pane {
    gap: 15px;
  }

  .info-section h3 {
    font-size: 16px;
    margin-bottom: 12px;
    padding-bottom: 8px;
    border-bottom-width: 2px;
  }

  .info-grid {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .info-item {
    padding: 8px 10px;
    font-size: 13px;

    .label {
      font-size: 12px;
    }

    .value {
      font-size: 13px;
    }
  }

  .special-abilities,
  .description-text {
    font-size: 13px;
    line-height: 1.6;

    p {
      margin: 6px 0;
    }
  }

  .equipment-list {
    gap: 10px;
  }

  .equipment-item {
    padding: 8px 10px;
    font-size: 13px;

    i {
      font-size: 18px;
    }
  }

  .inventory-grid {
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 10px;
  }

  .inventory-item {
    padding: 10px;
    border-width: 2px;

    .item-name {
      font-size: 13px;
    }

    .item-quantity {
      font-size: 11px;
    }

    .item-description {
      font-size: 10px;
    }
  }

  .notes-textarea {
    min-height: 120px;
    padding: 10px;
    font-size: 13px;
    border-width: 2px;
  }

  .stats-grid {
    gap: 10px;
  }

  .stat-item {
    gap: 12px;
    padding: 10px;
    font-size: 13px;

    i {
      font-size: 20px;
    }

    div {
      strong {
        font-size: 13px;
      }

      span {
        font-size: 12px;
      }
    }
  }

  .empty-state {
    padding: 30px 15px;

    i {
      font-size: 36px;
      margin-bottom: 12px;
    }

    p {
      font-size: 14px;
    }
  }

  .action-buttons {
    gap: 8px;
    padding-top: 15px;
    border-top-width: 2px;
    flex-wrap: wrap;
  }

  .action-btn {
    flex: 1 1 calc(50% - 4px);
    min-width: auto;
    padding: 10px 12px;
    font-size: 13px;
    border-width: 2px;
    gap: 6px;
    min-height: 44px; // 

    &:hover {
      transform: translateY(-1px);
    }
  }
}

// 
@media (max-width: 480px) {
  .npc-detail-panel {
    padding: 12px;
  }

  .npc-name {
    font-size: 20px;
  }

  .npc-meta-info {
    font-size: 12px;
  }

  .tab-button {
    flex: 1 1 100%;
    font-size: 12px;
  }

  .info-section h3 {
    font-size: 15px;
  }

  .action-btn {
    flex: 1 1 100%;
    font-size: 12px;
  }

  .inventory-grid {
    grid-template-columns: 1fr;
  }
}
