
.spell-detail-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3000;
  padding: 20px;
}

.spell-detail-modal {
  background-color: #fff;
  border: 4px solid #000;
  width: 90%;
  max-width: 800px;
  max-height: 85vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
  }
}

.modal-header {
  background-color: #fff;
  border-bottom: 3px solid #000;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;

  h2 {
    font-family: '', serif;
    font-size: 22px;
    font-weight: bold;
    margin: 0;
  }

  .close-btn {
    background: none;
    border: 2px solid #000;
    width: 40px;
    height: 40px;
    cursor: pointer;
    font-size: 20px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;

    &:hover {
      background-color: #000;
      color: #fff;
    }
  }
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: #fff;

  &.empty {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #999;
    font-style: italic;
  }
}

.spell-header {
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 3px solid #000;

  .spell-title-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    .spell-name {
      font-family: '', serif;
      font-size: 24px;
      font-weight: bold;
      margin: 0;
      color: #9370db;
    }

    .spell-level-badge {
      padding: 5px 15px;
      background-color: #9370db;
      color: #fff;
      border: 2px solid #000;
      font-weight: bold;
      font-size: 14px;
    }
  }

  .spell-english {
    font-size: 14px;
    color: #666;
    font-style: italic;
    margin: 0;
  }
}

.spell-properties {
  background-color: #f9f9f9;
  border: 2px solid #000;
  padding: 15px;
  margin-bottom: 20px;

  .property-row {
    display: flex;
    padding: 8px 0;
    border-bottom: 1px solid #ddd;

    &:last-child {
      border-bottom: none;
    }

    .property-label {
      font-weight: bold;
      min-width: 100px;
      color: #333;
    }

    .property-value {
      flex: 1;
      color: #666;

      &.material {
        color: #8b4513;
        font-style: italic;
      }

      &.reversible {
        color: #9370db;
        font-weight: bold;
      }
    }
  }
}

.spell-description {
  h4 {
    font-family: '', serif;
    font-size: 18px;
    font-weight: bold;
    margin: 0 0 15px 0;
    padding-bottom: 8px;
    border-bottom: 2px solid #000;
  }

  p {
    font-family: '', serif;
    font-size: 15px;
    line-height: 1.8;
    color: #333;
    text-align: justify;
    margin: 0;
    white-space: pre-wrap;
  }
}

// 
@media (max-width: 992px) {
  .spell-detail-overlay {
    padding: 10px;
  }

  .spell-detail-modal {
    width: 100%;
    max-width: 100%;
    max-height: 95vh;
    border-width: 3px;

    &::before {
      top: 4px;
      left: 4px;
      right: 4px;
      bottom: 4px;
    }
  }

  .modal-header {
    padding: 12px 15px;
    border-bottom-width: 2px;

    h3 {
      font-size: 18px;
    }

    .close-btn {
      width: 36px;
      height: 36px;
      font-size: 18px;
      border-width: 2px;
    }
  }

  .modal-body {
    padding: 15px;
  }

  .no-spell-selected {
    padding: 40px 15px;

    i {
      font-size: 40px;
      margin-bottom: 15px;
    }

    p {
      font-size: 14px;
    }
  }

  .spell-header {
    margin-bottom: 15px;
    padding-bottom: 12px;
    border-bottom-width: 2px;

    .spell-title-row {
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;

      .spell-name {
        font-size: 20px;
      }

      .spell-level-badge {
        padding: 4px 12px;
        font-size: 13px;
        border-width: 2px;
      }
    }

    .spell-english {
      font-size: 12px;
    }
  }

  .spell-properties {
    padding: 12px;
    margin-bottom: 15px;
    border-width: 2px;

    .property-row {
      flex-direction: column;
      padding: 8px 0;

      .property-label {
        min-width: auto;
        font-size: 13px;
        margin-bottom: 4px;
      }

      .property-value {
        font-size: 13px;
      }
    }
  }

  .spell-description {
    h4 {
      font-size: 16px;
      margin-bottom: 12px;
      padding-bottom: 6px;
      border-bottom-width: 2px;
    }

    p {
      font-size: 14px;
      line-height: 1.7;
    }
  }
}

@media (max-width: 480px) {
  .spell-detail-modal {
    border-width: 2px;

    &::before {
      border-width: 1px;
    }
  }

  .modal-header {
    padding: 10px 12px;

    h3 {
      font-size: 16px;
    }

    .close-btn {
      width: 32px;
      height: 32px;
      font-size: 16px;
    }
  }

  .modal-body {
    padding: 12px;
  }

  .spell-header .spell-title-row .spell-name {
    font-size: 18px;
  }

  .spell-description {
    h4 {
      font-size: 15px;
    }

    p {
      font-size: 13px;
      line-height: 1.6;
    }
  }
}
