
.step11-character-sheet {
  padding: 24px;
  max-width: 1200px;
  margin: 0 auto;

  @media (max-width: 992px) {
    padding: 15px 10px;
  }
}

.sheet-header {
  text-align: center;
  margin-bottom: 32px;

  @media (max-width: 992px) {
    margin-bottom: 20px;
  }

  h2 {
    font-size: 32px;
    margin-bottom: 8px;
    color: #8b4513;
    text-transform: uppercase;
    letter-spacing: 2px;

    @media (max-width: 992px) {
      font-size: 24px;
      letter-spacing: 1px;
      margin-bottom: 6px;
    }
  }

  .subtitle {
    font-size: 16px;
    color: #666;

    @media (max-width: 992px) {
      font-size: 14px;
    }
  }
}

.character-sheet-container {
  margin-bottom: 24px;
}

.character-sheet {
  border: 3px solid #8b4513;
  background: #f9f6f0;
  font-family: '', serif;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);

  @media (max-width: 992px) {
    border-width: 2px;
  }

  section {
    border: 2px solid #8b4513;
    padding: 20px;
    margin: 16px;
    background: linear-gradient(to bottom, #fffef8, #f5f2e8);
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);

    @media (max-width: 992px) {
      padding: 15px;
      margin: 10px;
    }

    h3 {
      border-bottom: 2px solid #8b4513;
      text-transform: uppercase;
      letter-spacing: 2px;
      margin: 0 0 16px 0;
      padding-bottom: 8px;
      font-size: 20px;
      color: #8b4513;
      font-weight: bold;

      @media (max-width: 992px) {
        font-size: 18px;
        letter-spacing: 1px;
        margin-bottom: 12px;
        padding-bottom: 6px;
      }
    }

    h4 {
      font-size: 16px;
      margin: 16px 0 8px 0;
      text-decoration: underline;
      color: #8b4513;

      @media (max-width: 992px) {
        font-size: 15px;
        margin: 12px 0 6px 0;
      }
    }
  }
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 12px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    gap: 8px;
  }

  .info-item {
    display: flex;
    justify-content: space-between;
    padding: 8px 12px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;

    @media (max-width: 992px) {
      padding: 6px 10px;
      font-size: 14px;
    }

    &.full-width {
      grid-column: 1 / -1;
    }

    .label {
      font-weight: bold;
      color: #666;
    }

    .value {
      color: #000;

      &.bonus {
        color: #28a745;
        font-weight: bold;
      }

      &.penalty {
        color: #dc3545;
        font-weight: bold;
      }
    }
  }
}

.abilities-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;

  @media (max-width: 992px) {
    grid-template-columns: 1fr;
    gap: 10px;
  }

  .ability-box {
    border: 2px solid #8b4513;
    padding: 16px;
    text-align: center;
    background: #fff;
    border-radius: 8px;

    @media (max-width: 992px) {
      padding: 12px;
    }

    .ability-name {
      font-size: 14px;
      font-weight: bold;
      color: #666;
      margin-bottom: 8px;

      @media (max-width: 992px) {
        font-size: 13px;
        margin-bottom: 6px;
      }
    }

    .ability-value {
      font-size: 28px;
      font-weight: bold;
      color: #8b4513;
      margin-bottom: 4px;

      @media (max-width: 992px) {
        font-size: 24px;
      }
    }

    .ability-adjust {
      font-size: 12px;
      color: #666;
      margin-bottom: 8px;

      @media (max-width: 992px) {
        font-size: 11px;
        margin-bottom: 6px;
      }
    }

    .ability-modifiers {
      margin-top: 12px;
      padding-top: 12px;
      border-top: 1px solid #ddd;
      text-align: left;

      @media (max-width: 992px) {
        margin-top: 10px;
        padding-top: 10px;
      }

      .modifier-line {
        font-size: 11px;
        line-height: 1.6;
        color: #333;
        margin-bottom: 4px;

        @media (max-width: 992px) {
          font-size: 10px;
          line-height: 1.5;
        }

        &.primary {
          font-weight: bold;
          color: #8b4513;
        }

        &.secondary {
          color: #666;
        }
      }
    }
  }
}

.exceptional-strength {
  margin-top: 16px;
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  color: #8b4513;
}

.combat-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 12px;
  margin-bottom: 16px;

  @media (max-width: 992px) {
    grid-template-columns: repeat(2, 1fr);
    gap: 8px;
    margin-bottom: 12px;
  }

  @media (max-width: 480px) {
    grid-template-columns: 1fr;
  }

  .combat-item {
    display: flex;
    justify-content: space-between;
    padding: 8px 12px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 4px;

    @media (max-width: 992px) {
      padding: 6px 10px;
      font-size: 14px;
    }

    &.full-width {
      grid-column: 1 / -1;
      background: #fffdf7;
      border-color: #daa520;
      padding: 6px 12px;

      @media (max-width: 992px) {
        padding: 5px 10px;
      }
    }

    .label {
      font-weight: bold;
      color: #666;
    }

    .detail {
      font-size: 14px;
      color: #888;
      margin-left: 8px;

      @media (max-width: 992px) {
        font-size: 12px;
      }
    }

    .ac-note {
      font-size: 13px;
      color: #666;
      font-style: italic;

      @media (max-width: 992px) {
        font-size: 12px;
      }
    }

    .value {
      color: #000;
      font-weight: bold;
    }
  }
}

.attack-bonuses,
.damage-bonuses {
  background: white;
  padding: 12px;
  border-radius: 4px;
  border: 1px solid #ddd;
  margin-bottom: 16px;

  @media (max-width: 992px) {
    padding: 10px;
    margin-bottom: 12px;
  }

  h4 {
    margin: 0 0 12px 0 !important;
    font-size: 16px !important;
    color: #8b4513;
    text-decoration: none !important;
    border-bottom: 1px solid #ddd;
    padding-bottom: 8px;

    @media (max-width: 992px) {
      font-size: 15px !important;
      margin-bottom: 10px !important;
      padding-bottom: 6px;
    }
  }

  .bonus-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 12px;

    @media (max-width: 992px) {
      gap: 10px;
    }

    @media (max-width: 480px) {
      grid-template-columns: 1fr;
    }
  }

  .bonus-item {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 8px;
    background: #f9f9f9;
    border-radius: 4px;

    @media (max-width: 992px) {
      padding: 6px;
      font-size: 14px;
    }

    .bonus-label {
      color: #666;
      font-weight: 500;
    }

    .bonus-value {
      font-weight: bold;
      color: #8b4513;
      font-size: 18px;

      @media (max-width: 992px) {
        font-size: 16px;
      }
    }

    .bonus-source {
      color: #888;
      font-size: 13px;
      font-style: italic;

      @media (max-width: 992px) {
        font-size: 12px;
      }
    }
  }

  .damage-note {
    margin-top: 8px;
    font-size: 13px;
    color: #666;
    font-style: italic;

    @media (max-width: 992px) {
      font-size: 12px;
      margin-top: 6px;
    }
  }
}

.saving-throws {
  background: white;
  padding: 12px;
  border-radius: 4px;
  border: 1px solid #ddd;

  @media (max-width: 992px) {
    padding: 10px;
  }

  .throws-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 8px;
    font-size: 14px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
      gap: 6px;
      font-size: 13px;
    }
  }
}

.proficiency-subsection {
  margin-bottom: 16px;

  ul {
    list-style-type: disc;
    padding-left: 24px;
    margin: 8px 0;

    li {
      margin: 4px 0;
    }
  }
}

.money-info {
  display: flex;
  gap: 24px;
  margin-bottom: 16px;
  font-weight: bold;
  font-size: 16px;
}

.equipment-list {
  .equipment-group {
    margin-bottom: 16px;

    ul {
      list-style-type: disc;
      padding-left: 24px;
      margin: 8px 0;

      li {
        margin: 4px 0;

        .weight {
          color: #666;
          font-size: 14px;
        }
      }
    }
  }
}

.spell-slots {
  margin-bottom: 16px;

  .slots-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 8px;
    margin-top: 8px;

    .bonus {
      color: #28a745;
      font-weight: bold;
    }
  }
}

.spellbook,
.spell-spheres {
  margin-top: 16px;

  div {
    margin: 8px 0;
  }
}

.info-subsection {
  margin-bottom: 16px;

  p {
    white-space: pre-wrap;
    line-height: 1.6;
  }
}

.abilities-list {
  .ability-item {
    margin: 12px 0;
    line-height: 1.6;

    strong {
      color: #8b4513;
    }
  }
}

.subsection {
  margin-top: 16px;

  ul {
    list-style-type: disc;
    padding-left: 24px;
    margin: 8px 0;

    li {
      margin: 4px 0;
      line-height: 1.6;
    }
  }
}

.empty-text {
  color: #999;
  font-style: italic;
}

.bottom-actions {
  display: flex;
  justify-content: space-between;
  gap: 16px;
  padding: 0 16px;

  @media (max-width: 992px) {
    gap: 10px;
    padding: 0 10px;
  }

  .adnd-button {
    flex: 1;
    max-width: 300px;
    padding: 16px 32px;
    font-size: 18px;
    font-weight: bold;
    border: 2px solid #8b4513;
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;

    @media (max-width: 992px) {
      max-width: none;
      padding: 14px 24px;
      font-size: 16px;
      min-height: 44px;
    }

    .button-icon {
      font-size: 20px;

      @media (max-width: 992px) {
        font-size: 18px;
      }
    }

    &.primary {
      background: linear-gradient(135deg, #d4af37, #c9a227);
      color: white;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);

      &:hover:not(:disabled) {
        background: linear-gradient(135deg, #e0bb44, #d4af37);
        transform: translateY(-2px);
        box-shadow: 0 6px 8px rgba(0, 0, 0, 0.4);
      }

      &:disabled {
        opacity: 0.6;
        cursor: not-allowed;
      }
    }

    &.secondary {
      background: white;
      color: #8b4513;

      &:hover {
        background: #f5f2e8;
        transform: translateY(-2px);
      }
    }
  }
}
