
// ADND 2E 
.adnd-status-panel {
  width: 280px;
  height: 100%;
  background-color: #fff;
  border-right: 4px solid #000;
  display: flex;
  flex-direction: column;
  font-family: '', serif;
  color: #000;
  box-shadow: inset 0 0 0 2px #666;

  &.in-modal {
    width: 100%;
    height: auto;
    border: 3px solid #000;
    box-shadow: none;
  }
}

.panel-tabs {
  display: flex;
  background-color: #fff;
  border-bottom: 3px solid #000;
  overflow-x: auto;

  .tab-item {
    flex: 1;
    padding: 10px 8px;
    text-align: center;
    cursor: pointer;
    transition: all 0.2s;
    border-right: 2px solid #000;
    font-size: 11px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    background-color: #fff;
    color: #000;
    white-space: nowrap;

    &:last-child {
      border-right: none;
    }

    &:hover {
      background-color: #000;
      color: #fff;
    }

    &.active {
      background-color: #000;
      color: #fff;
      box-shadow: inset 0 0 0 2px #666;
    }

    i {
      display: block;
      margin-bottom: 4px;
      font-size: 14px;
    }
  }
}

.panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  background-color: #f5f5f5;
  background-image: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 1px,
    rgba(0, 0, 0, 0.03) 1px,
    rgba(0, 0, 0, 0.03) 2px
  );
}

.tab-pane {
  animation: fadeIn 0.3s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.info-row {
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
  border-bottom: 2px solid #000;
  background-color: #fff;
  margin-bottom: 2px;

  &.section-divider {
    background-color: #f5f5f5;
    border-bottom: 3px solid #8b4513;
    padding: 6px 12px;
    margin-top: 8px;
  }

  &.ac-note-row,
  &.damage-note-row {
    background-color: #fffdf7;
    border-color: #daa520;
    padding: 4px 12px;
  }

  .label {
    color: #000;
    font-size: 13px;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 0.5px;

    &.section-title-small {
      font-size: 14px;
      color: #8b4513;
      font-weight: bold;
    }

    &.sub-label {
      font-size: 12px;
      padding-left: 16px;
      color: #666;
    }
  }

  .value {
    color: #000;
    font-weight: bold;
    font-size: 14px;
    font-family: '', serif;

    &.highlight {
      color: #d4af37;
      font-size: 16px;
    }

    &.deity-rank {
      color: #8b4513;
      font-size: 15px;
    }

    &.bonus-value {
      display: flex;
      align-items: center;
      gap: 6px;
      color: #8b4513;
      font-size: 16px;
    }

    // HP 
    &.hp-low {
      color: #ff9800;
      animation: pulse-warning 2s ease-in-out infinite;
    }

    &.hp-critical {
      color: #f44336;
      animation: pulse-critical 1s ease-in-out infinite;
    }
  }

  .ac-detail {
    font-size: 11px;
    color: #888;
    margin-left: 6px;
    font-weight: normal;
  }

  .ac-note,
  .damage-note {
    font-size: 11px;
    color: #666;
    font-style: italic;
    font-weight: normal;
  }

  .bonus-source {
    font-size: 11px;
    color: #888;
    font-weight: normal;
    font-style: italic;
  }
}

// HP 
@keyframes pulse-warning {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

@keyframes pulse-critical {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.05);
  }
}

