    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
  }

  .deity-warning {
    padding: 8px 10px;
    margin-bottom: 12px;
    background-color: rgba(255, 193, 7, 0.15);
    border-left: 4px solid #ff9800;
    font-size: 11px;
