        margin-bottom: 6px;
      }

      p {
        font-size: 11px;
        color: #bf360c;
        line-height: 1.5;
        margin: 4px 0;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  //  
  .deity-attributes-notice {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    padding: 12px;
    margin-bottom: 16px;
    background: linear-gradient(135deg, #e3f2fd 0%, #bbdefb 100%);
    border: 2px solid #2196f3;
    border-radius: 6px;

    .notice-icon {
      font-size: 24px;
      line-height: 1;
      flex-shrink: 0;
    }

    .notice-text {
      flex: 1;

      strong {
        display: block;
        font-size: 13px;
        color: #0d47a1;
        margin-bottom: 6px;
      }

      p {
        font-size: 11px;
        color: #1565c0;
        line-height: 1.5;
        margin: 4px 0;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  //  ""
  .deity-na {
    color: #999 !important;
    font-style: italic;
    font-size: 12px;
    opacity: 0.7;
  }

  //  
  .deity-avatar-note {
    background-color: rgba(33, 150, 243, 0.05);
    border-left: 3px solid #2196f3;
    padding: 8px 12px !important;
    margin: 4px 0 !important;

    .label {
      font-size: 11px;
      color: #1976d2;
      font-weight: bold;
    }

    .small-text {
      font-size: 10px;
      color: #555;
