.saving-throws-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  padding: 10px 12px;

  .saving-throw-item {
    display: flex;
    flex-direction: column;
    padding: 8px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 3px;
    transition: all 0.2s;

    &:hover {
      background-color: #f0f0f0;
      border-color: #8b4513;
    }

    &.deity-st {
      background: linear-gradient(135deg, #fff9e6 0%, #fff3cc 100%);
      border-color: #d4af37;

      .st-value {
        color: #d4af37;
        font-weight: bold;
      }
    }

    .st-label {
      font-size: 10px;
      color: #666;
      margin-bottom: 4px;
      font-weight: 500;
      line-height: 1.3;
    }

    .st-value {
      font-size: 16px;
      font-weight: bold;
      color: #8b4513;
      font-family: '', serif;
    }
  }
}

