      color: #000;
      background-color: #f0f0f0;
      padding: 2px 8px;
      border: 2px solid #000;
    }
  }

  .skill-description {
    font-size: 11px;
    line-height: 1.4;
    color: #333;
    margin-bottom: 6px;
  }

  .skill-breakdown {
    font-family: '', serif;
    font-size: 10px;
    color: #666;
    font-style: italic;
    padding-top: 4px;
    border-top: 1px dashed #ddd;
  }
}

// 
.proficiency-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10000;
  animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.proficiency-modal-content {
  background-color: #f5e6d3;
  border: 4px solid #000;
  box-shadow: 8px 8px 0 rgba(0, 0, 0, 0.5);
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: relative;
  animation: slideIn 0.3s ease;

  &::before {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    right: 6px;
    bottom: 6px;
    border: 2px solid #666;
    pointer-events: none;
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.modal-header {
  background-color: #000;
  color: #fff;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 3px solid #000;

  h2 {
    margin: 0;
    font-size: 18px;
    font-family: '', serif;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  .close-button {
    background: none;
    border: 2px solid #fff;
    color: #fff;
    font-size: 20px;
    width: 32px;
    height: 32px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;

    &:hover {
      background-color: #fff;
      color: #000;
    }
  }
}

.modal-body {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.prof-info-row {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  padding: 8px 12px;
  background-color: #fff;
  border: 2px solid #000;
  border-left: 4px solid #000;

  .prof-label {
    font-weight: bold;
    font-size: 13px;
    font-family: '', serif;
    min-width: 100px;
    color: #000;
  }

  .prof-value {
    font-size: 13px;
    font-family: '', serif;
    color: #333;
  }
}

.prof-description {
  margin-top: 20px;
  padding: 15px;
  background-color: #fff;
  border: 3px solid #000;
  box-shadow: 3px 3px 0 #000;
  position: relative;

  &::before {
    content: '';
    position: absolute;
    top: 4px;
    left: 4px;
    right: 4px;
    bottom: 4px;
    border: 1px solid #666;
    pointer-events: none;
  }

  h3 {
    margin: 0 0 12px 0;
    font-size: 14px;
    font-family: '', serif;
    text-transform: uppercase;
    letter-spacing: 1px;
    color: #000;
    border-bottom: 2px solid #000;
    padding-bottom: 8px;
  }

  p {
    margin: 0;
    font-size: 13px;
    line-height: 1.6;
    color: #333;
    white-space: pre-wrap;
    font-family: '', serif;
  }
}

// ====================  ====================
.turn-undead-modal {
  .turn-undead-content {
    max-width: 900px;
    width: 95%;
  }

  .turn-undead-body {
    overflow-y: auto;
    max-height: 70vh;
  }

  .turn-section {
    margin-bottom: 24px;
    padding: 15px;
    background-color: #fff;
    border: 3px solid #000;
    box-shadow: 3px 3px 0 #000;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 4px;
      left: 4px;
      right: 4px;
      bottom: 4px;
      border: 1px solid #666;
      pointer-events: none;
    }

    h3 {
      margin: 0 0 15px 0;
      font-size: 16px;
      font-family: '', serif;
      text-transform: uppercase;
      letter-spacing: 1px;
      color: #000;
      border-bottom: 2px solid #000;
      padding-bottom: 8px;
    }

    p {
      font-size: 13px;
      line-height: 1.7;
      color: #333;
      margin: 10px 0;
      font-family: '', serif;
    }

    ul {
      margin: 10px 0;
      padding-left: 25px;
      font-size: 13px;
      line-height: 1.7;
      color: #333;
      font-family: '', serif;

      li {
        margin: 8px 0;
      }
    }
  }

  .turn-example {
    background-color: #fffacd;

    p {
      font-style: italic;
    }
  }

  .turn-table-wrapper {
    overflow-x: auto;
    margin: 15px 0;
  }

  .turn-undead-table {
    width: 100%;
    border-collapse: collapse;
    background-color: #fff;
    font-family: '', serif;
    font-size: 12px;

    thead {
      background-color: #000;
      color: #fff;

      th {
        padding: 8px;
        border: 2px solid #000;
        font-weight: bold;
        text-align: center;
        white-space: nowrap;
      }
    }

    tbody {
      td {
        padding: 6px 8px;
        border: 2px solid #000;
        text-align: center;
        font-weight: bold;

        &.undead-name {
          text-align: left;
          background-color: #f5f5f5;
          white-space: nowrap;
        }

        &.result-auto-turn {
          background-color: #90ee90;
          color: #000;
        }

        &.result-auto-destroy {
          background-color: #ffd700;
          color: #000;
        }

        &.result-auto-destroy-plus {
          background-color: #ff6347;
          color: #fff;
        }

        &.result-impossible {
          background-color: #ddd;
          color: #999;
        }

        &.current-level {
          outline: 3px solid #1e90ff;
          outline-offset: -3px;
          box-shadow: inset 0 0 8px rgba(30, 144, 255, 0.4);
        }
      }
    }

    tr:nth-child(even) {
      td:not(.undead-name):not(.result-auto-turn):not(.result-auto-destroy):not(.result-auto-destroy-plus):not(
          .result-impossible
        ) {
        background-color: #f9f9f9;
      }
    }
  }

  .turn-notes {
    margin-top: 15px;
    padding: 10px;
    background-color: #fffacd;
    border: 2px solid #daa520;
    border-radius: 4px;

    .note-item {
      font-size: 11px;
      line-height: 1.5;
      color: #666;
      margin: 5px 0;
      font-style: italic;

      &::before {
        content: ' ';
        color: #daa520;
      }
    }
  }

  // ====================  ====================
  .deity-ability-modal {
    .deity-ability-content {
      max-width: 700px;
      width: 90%;
      background-color: #fef5e7;
    }

    .deity-ability-body {
      overflow-y: auto;
      max-height: 70vh;
    }

    .deity-ability-info-section {
      margin-bottom: 20px;
      padding: 15px;
      background-color: #fff;
      border: 3px solid #d4af37;
      box-shadow: 3px 3px 0 #d4af37;

      .info-item {
        display: flex;
        align-items: flex-start;
        margin-bottom: 12px;
        gap: 8px;

        &:last-child {
          margin-bottom: 0;
        }

        &.description {
          flex-direction: column;
          gap: 4px;
        }

        .info-label {
          font-weight: bold;
          font-size: 13px;
          color: #8b4513;
          min-width: 80px;
          flex-shrink: 0;
        }

        .info-value {
          font-size: 13px;
          color: #333;
          line-height: 1.6;
          flex: 1;
        }
      }
    }

    .deity-ability-details,
    .deity-ability-limitations {
      margin-bottom: 20px;
      padding: 15px;
      background-color: #fff;
      border: 3px solid #000;
      box-shadow: 3px 3px 0 #000;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 4px;
        left: 4px;
        right: 4px;
        bottom: 4px;
        border: 1px solid #666;
        pointer-events: none;
      }

      h3 {
        margin: 0 0 15px 0;
        font-size: 15px;
        font-family: '', serif;
        text-transform: uppercase;
        letter-spacing: 1px;
        color: #000;
        border-bottom: 2px solid #000;
        padding-bottom: 8px;
      }

      ul {
        margin: 0;
        padding-left: 20px;
        font-size: 13px;
        line-height: 1.7;
        color: #333;

        li {
          margin: 8px 0;
          position: relative;

          &::marker {
            color: #8b4513;
          }
        }
      }
    }

    .deity-ability-limitations {
      background-color: #fffacd;
      border-color: #ffa500;

      h3 {
        color: #d84315;
        border-bottom-color: #ffa500;
      }

      ul li::marker {
        color: #d84315;
      }
    }
  }

  // 
  .extra-abilities-section {
    .section-header {
      margin-bottom: 16px;
      padding: 12px;
      background-color: #f8f8f8;
      border: 2px solid #000;
      border-radius: 4px;

      h3 {
        margin: 0 0 8px 0;
        font-size: 16px;
        font-weight: bold;
        color: #000;

        i {
          margin-right: 8px;
          color: #8b4513;
        }
      }

      .section-hint {
        margin: 0;
        font-size: 12px;
        color: #666;
        font-style: italic;
      }
    }

    .extra-ability-card {
      padding: 12px;
      margin-bottom: 12px;
      background-color: #fff;
      border: 2px solid #000;
      border-radius: 4px;
      transition: all 0.2s;

      &:hover {
        background-color: #f9f9f9;
        box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.15);
      }

      &:last-child {
        margin-bottom: 0;
      }

      .ability-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 8px;
        padding-bottom: 8px;
        border-bottom: 1px solid #ddd;

        .ability-name {
          font-size: 14px;
          font-weight: bold;
          color: #8b4513;
        }

        .ability-source {
          font-size: 11px;
          color: #666;
          font-style: italic;
        }
      }

      .ability-description,
      .ability-effect,
      .ability-conditions,
      .ability-uses {
        margin-top: 6px;
        font-size: 12px;
        line-height: 1.5;
        color: #333;

        strong {
          color: #000;
          font-weight: bold;
        }
      }

      .ability-description {
        color: #555;
      }

      .ability-effect {
        padding: 6px 8px;
        background-color: #f0f8ff;
        border-left: 3px solid #4682b4;
        border-radius: 2px;
      }

      .ability-conditions {
        padding: 6px 8px;
        background-color: #fff8dc;
        border-left: 3px solid #daa520;
        border-radius: 2px;
      }

      .ability-uses {
        padding: 6px 8px;
        background-color: #f5f5f5;
        border-left: 3px solid #696969;
        border-radius: 2px;
      }
    }

    .empty-state {
      text-align: center;
      padding: 32px 16px;
      color: #999;

      .empty-icon {
        font-size: 48px;
        margin-bottom: 16px;
        color: #ccc;
      }

      .empty-text {
        font-size: 14px;
        font-weight: bold;
        margin-bottom: 8px;
        color: #666;
      }

      .empty-hint {
        font-size: 12px;
        color: #999;
        font-style: italic;
      }
    }
  }
}

// ====================  ====================
@media (max-width: 992px) {
  .adnd-status-panel {
    // 
    .panel-tabs {
      flex-wrap: wrap;
      gap: 6px;
      padding: 8px;

      .tab-item {
        flex: 1 1 calc(50% - 3px);
