        .stat-label {
          min-width: 70px;
          font-size: 10px;
        }

        .stat-value {
          font-size: 10px;
        }
      }
    }

    // 
    .thief-skill-item {
      padding: 8px;
      border-width: 2px;

      &::before {
        display: none;
      }

      .skill-name-row {
        .skill-name {
          font-size: 12px;
        }

        .skill-value {
          font-size: 14px;
          padding: 2px 6px;
        }
      }

      .skill-description {
        font-size: 10px;
      }

      .skill-breakdown {
