        }
      }

      .notice-hint {
        font-size: 11px;
      }

      .notice-example {
        font-size: 10px;
      }
    }

    // 
    .mr-sources,
    .mr-sources-combat {
      font-size: 10px;
    }

    // 
    .deity-ability-modal {
      .deity-ability-info-section {
        padding: 12px;

        .info-item {
          .info-label {
            font-size: 12px;
            min-width: 70px;
          }

          .info-value {
            font-size: 12px;
          }
        }
      }

      .deity-ability-details,
      .deity-ability-limitations {
        padding: 12px;

        h3 {
          font-size: 14px;
        }

        ul {
          font-size: 12px;
        }
      }
    }

    // 
    .empty-text {
      padding: 8px;
      font-size: 12px;
    }

    .empty-state {
      padding: 24px 12px;

      .empty-icon {
        font-size: 36px;
      }

      .empty-text {
        font-size: 13px;
      }

      .empty-hint {
        font-size: 11px;
      }
    }

    // 
    .ammunition-row {
      padding: 5px;

      .stat-label,
      .stat-value {
        font-size: 10px;
      }
    }
  }

  // 
  .adnd-status-panel.in-modal {
    .panel-tabs .tab-item {
      flex: 1 1 calc(33.33% - 4px);
      min-width: calc(33.33% - 4px);
    }
  }
}

// 480px
@media (max-width: 480px) {
  .adnd-status-panel {
    .panel-tabs .tab-item {
      flex: 1 1 100%;
      min-width: 100%;
      padding: 8px 6px;
      font-size: 11px;
    }

    .ability-score {
      font-size: 18px !important;
    }

    .section-title {
      font-size: 11px;
      padding: 5px 8px;
    }

    .proficiency-modal-content,
    .turn-undead-content,
    .deity-ability-content {
      .modal-header h2 {
        font-size: 14px;
      }

      .modal-body {
        padding: 12px;
      }
    }

    .turn-undead-table {
      font-size: 9px;

      thead th,
      tbody td {
        padding: 3px 2px;
      }
    }
  }
}
