
.modal-overlay {
  position: fixed !important;
  top: 0 !important;
  left: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 20px;
  overflow: auto;
}

.modal-card {
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  max-width: 1200px;
  width: 95%;
  max-height: 90vh;
  display: flex;
  flex-direction: column;
  position: relative;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.modal-header {
  background-color: #fff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 8px 8px 0 0;
  padding: 18px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  gap: 15px;
  cursor: move;
  user-select: none;

  h2 {
    font-family: '', serif;
    font-size: 20px;
    font-weight: 600;
    letter-spacing: 1px;
    margin: 0;
    color: #333;
  }
}

.header-controls {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-left: auto;

  .chapter-size-input {
    width: 60px;
    padding: 6px 8px;
    border: 2px solid #000;
    background-color: #fff;
    font-family: '', serif;
    font-size: 13px;
    text-align: center;

    &:focus {
      outline: none;
      border-color: #4a90e2;
    }
  }

  .input-label {
    font-family: '', serif;
    font-size: 12px;
    color: #666;
  }
}

.close-button {
  background: transparent;
  border: none;
  width: 32px;
  height: 32px;
  cursor: pointer;
  font-size: 20px;
  font-weight: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  color: #666;
  border-radius: 4px;

  &:hover {
    background-color: #f5f5f5;
    color: #333;
  }
}

.chapter-nav {
  background-color: #fafafa;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  padding: 12px 24px;
  display: flex;
  align-items: center;
  gap: 15px;
  z-index: 2;
  flex-wrap: wrap;
}

.display-mode-toggle {
  display: flex;
  gap: 15px;

  .radio-label {
    display: flex;
    align-items: center;
    gap: 6px;
    font-family: '', serif;
    font-size: 13px;
    cursor: pointer;

    input[type='radio'] {
      cursor: pointer;
    }
  }
}

.chapter-selector {
  display: flex;
  align-items: center;
  gap: 10px;
  flex: 1;
}

.nav-btn {
  width: 32px;
  height: 32px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  color: #666;

  &:hover:not(:disabled) {
    background-color: #f5f5f5;
    border-color: rgba(0, 0, 0, 0.25);
  }

  &:disabled {
    opacity: 0.4;
    cursor: not-allowed;
  }
}

.chapter-select {
  flex: 1;
  padding: 6px 12px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  background-color: #fff;
  font-family: '', serif;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;

  &:focus {
    outline: none;
    border-color: #4a90e2;
    box-shadow: 0 0 0 3px rgba(74, 144, 226, 0.1);
  }
}

.bookmark-btn {
  padding: 6px 14px;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
  font-family: '', serif;
  font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.2s;
  color: #666;

  &:hover {
    background-color: #f5f5f5;
    border-color: rgba(0, 0, 0, 0.25);
    color: #333;
  }

  i {
    font-size: 14px;
  }
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  padding: 24px;
  background-color: #fff;

  &.continuous-mode {
    padding: 0;
  }
}

.single-chapter-view {
  .chapter-header {
    text-align: center;
    margin-bottom: 20px;
    padding-bottom: 12px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);

    h3 {
      font-family: '', serif;
      font-size: 18px;
      font-weight: 600;
      margin: 0 0 8px 0;
      letter-spacing: 1px;
      color: #333;
    }

    .chapter-range {
      font-family: '', serif;
      font-size: 13px;
      color: #999;
      margin: 0;
      font-weight: normal;
    }
  }
}

.continuous-view {
  .chapter-block {
    position: relative;
  }

  .chapter-anchor {
    position: absolute;
    top: -80px;
  }
}

.chapter-divider {
  background-color: #fafafa;
  padding: 24px 20px;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  margin: 20px 0;

  .divider-content {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    font-family: '', serif;
  }

  .divider-icon {
    font-size: 18px;
    color: #8b4513;
  }

  .chapter-title {
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 1px;
    color: #333;
  }

  .chapter-info {
    font-size: 12px;
    color: #666;
    font-family: '', serif;
  }
}

.chapter-content,
.chapter-messages {
  display: flex;
  flex-direction: column;
  gap: 15px;
  padding: 0 20px 20px;
}

.message-item {
  background-color: #fafafa;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 6px;
  padding: 16px;
  position: relative;
  transition: all 0.2s;

  &:hover {
    background-color: #f5f5f5;
    border-color: rgba(0, 0, 0, 0.12);
  }

  &.message-system {
    background-color: #fafafa;
    border-left: 3px solid #ff9800;
  }

  &.message-user {
    background-color: #fafafa;
    border-left: 3px solid #4a90e2;
  }

  &.message-assistant {
    background-color: #fafafa;
    border-left: 3px solid #28a745;
  }
}

.message-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 12px;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
}

.message-role {
  font-family: '', serif;
  font-size: 11px;
  font-weight: 500;
  padding: 3px 10px;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.message-time {
  font-family: '', serif;
  font-size: 11px;
  color: #999;
  font-weight: normal;
}

.message-index {
  font-family: '', serif;
  font-size: 11px;
  color: #999;
  font-weight: normal;
}

.message-user .message-role {
  background-color: rgba(74, 144, 226, 0.15);
  color: #4a90e2;
}

.message-assistant .message-role {
  background-color: rgba(40, 167, 69, 0.15);
  color: #28a745;
}

.message-system .message-role {
  background-color: rgba(255, 152, 0, 0.15);
  color: #ff9800;
}

.snapshot-badge {
  margin-left: 8px;
  padding: 3px 8px;
  background-color: rgba(255, 215, 0, 0.2);
  color: #d4a017;
  font-size: 11px;
  border-radius: 10px;
  font-weight: 500;
  display: inline-flex;
  align-items: center;

  i {
    font-size: 12px;
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.6;
  }
}

.message-actions {
  display: flex;
  gap: 6px;
  margin-left: auto;
}

.message-action-btn {
  background: transparent;
  border: none;
  width: 28px;
  height: 28px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
  font-size: 14px;
  color: #999;
  border-radius: 4px;

  &:hover {
    background-color: #f0f0f0;
    color: #333;
  }

  &.edit-btn:hover {
    background-color: rgba(74, 144, 226, 0.1);
    color: #4a90e2;
  }

  &.delete-btn:hover {
    background-color: rgba(220, 53, 69, 0.1);
    color: #dc3545;
  }
}

.message-content {
  font-family: '', serif;
  font-size: 14px;
  line-height: 1.6;
  color: #333;
  word-wrap: break-word;
  white-space: pre-wrap;
}

.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #999;

  i {
    font-size: 48px;
    margin-bottom: 20px;
    opacity: 0.5;
  }

  p {
    font-family: '', serif;
    font-size: 16px;
    margin: 10px 0;

    &.hint {
      font-size: 13px;
      color: #bbb;
    }
  }
}

.modal-footer {
  border-top: 3px solid #000;
  background-color: #fff;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
  gap: 15px;
  flex-wrap: wrap;
}

.footer-stats {
  display: flex;
  gap: 20px;
  font-family: '', serif;
  font-size: 12px;
  color: #666;
}

.footer-actions {
  display: flex;
  gap: 10px;
}

.footer-btn {
  padding: 8px 16px;
  border: 2px solid #000;
  background-color: #fff;
  cursor: pointer;
  font-family: '', serif;
  font-size: 12px;
  display: flex;
  align-items: center;
  gap: 6px;
  transition: all 0.2s;

  &:hover {
    background-color: #000;
    color: #fff;
  }

  &.danger {
    border-color: #dc3545;
    color: #dc3545;

    &:hover {
      background-color: #dc3545;
      border-color: #dc3545;
      color: #fff;
    }
  }
}

// 
.bookmark-modal {
  background-color: #fff;
  border: 4px solid #000;
  max-width: 500px;
  width: 90%;
  max-height: 70vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);

  .modal-header {
    border-bottom: 2px solid #000;

    h3 {
      font-family: '', serif;
      font-size: 18px;
      font-weight: bold;
      letter-spacing: 1px;
      margin: 0;
    }
  }
}

.bookmark-list {
  flex: 1;
  overflow-y: auto;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.bookmark-item {
  background-color: #fff;
  border: 2px solid #000;
  padding: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 10px;
  transition: all 0.2s;

  &:hover {
    background-color: #fffbf0;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  }
}

.bookmark-info {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  font-family: '', serif;

  i {
    color: #ffd700;
    font-size: 16px;
  }

  .bookmark-chapter {
    font-weight: bold;
    font-size: 14px;
  }

  .bookmark-time {
    font-family: '', serif;
    font-size: 11px;
    color: #999;
    margin-left: auto;
  }
}

.remove-bookmark-btn {
  background: none;
  border: 2px solid #000;
  width: 32px;
  height: 32px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;

  &:hover {
    background-color: #dc3545;
    border-color: #dc3545;
    color: #fff;
  }
}

// 
.edit-modal {
  background-color: #fff;
  border: 4px solid #000;
  max-width: 800px;
  width: 90%;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);

  .modal-header {
    border-bottom: 2px solid #000;

    h3 {
      font-family: '', serif;
      font-size: 18px;
      font-weight: bold;
      letter-spacing: 1px;
      margin: 0;
    }
  }

  .modal-body {
    flex: 1;
    overflow-y: auto;
    padding: 20px;
  }

  .modal-footer {
    border-top: 2px solid #000;
    background-color: #fff;
    padding: 15px 20px;
    display: flex;
    justify-content: flex-end;
    gap: 10px;

    .primary {
      background-color: #4a90e2;
      border-color: #4a90e2;
      color: #fff;

      &:hover {
        background-color: #357abd;
        border-color: #357abd;
      }
    }
  }
}

.edit-form {
  display: flex;
  flex-direction: column;
  gap: 10px;

  .form-label {
    font-family: '', serif;
    font-size: 14px;
    font-weight: bold;
    color: #333;
  }
}

.edit-textarea {
  width: 100%;
  padding: 12px;
  border: 2px solid #000;
  background-color: #fff;
  font-family: '', serif;
  font-size: 13px;
  line-height: 1.6;
  resize: vertical;

  &:focus {
    outline: none;
    border-color: #4a90e2;
  }
}

// 
@media (max-width: 992px) {
  .modal-card {
    max-width: 100%;
    width: 100%;
    max-height: 100vh;
    height: 100vh;
    border-radius: 0;
    border: none;
  }

  .chapter-nav {
    flex-direction: column;
    align-items: stretch;
  }

  .chapter-selector {
    width: 100%;
  }

  .footer-stats,
  .footer-actions {
    width: 100%;
    justify-content: center;
  }

  .edit-modal {
    max-width: 95%;
    width: 95%;
  }
}
