
// ====================  ====================
.monster-encyclopedia-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  animation: fadeIn 0.2s ease-out;
  padding: 20px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

// ====================  ====================
.monster-encyclopedia-modal {
  background-color: #fff;
  border: 3px solid #2c2c2c;
  border-radius: 12px;
  width: 96vw; // 
  max-width: 1800px; // 
  height: 94vh; // 
  max-height: 94vh; // 
  display: flex;
  flex-direction: column;
  box-shadow: 0 12px 48px rgba(0, 0, 0, 0.3);
  animation: slideUp 0.3s ease-out;
  position: relative;
  overflow: hidden; // 

  // 
  &::before {
    content: '';
    position: absolute;
    top: 8px;
    left: 8px;
    right: 8px;
    bottom: 8px;
    border: 1px solid rgba(0, 0, 0, 0.08);
    pointer-events: none;
    z-index: 1;
    border-radius: 8px;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

// ====================  ====================
.decoration-header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 20px 8px; //  padding
  background: linear-gradient(to bottom, #f8f8f8, #fff);
  border-bottom: 1px solid rgba(0, 0, 0, 0.06);
  z-index: 2;
  position: relative;
  flex-shrink: 0; // 

  .demon-decoration {
    height: 90px; //  100px  90px
    width: auto;
    object-fit: contain;
    filter: drop-shadow(0 4px 8px rgba(0, 0, 0, 0.15));
    animation: float 3s ease-in-out infinite;
  }
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

// ====================  ====================
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 30px; //  15px  12px
  border-bottom: 2px solid rgba(0, 0, 0, 0.08);
  background-color: #fff;
  z-index: 2;
  position: relative;
  flex-shrink: 0; // 

  .modal-title {
    margin: 0;
    font-size: 22px; //  24px  22px
    color: #2c2c2c;
    font-family: '', serif;
    font-weight: 700;
    letter-spacing: 2px;
    display: flex;
    align-items: center;
    gap: 10px;

    i {
      color: #8b4513;
      font-size: 26px; //  28px  26px
    }
  }

  .close-btn {
    background: none;
    border: 2px solid rgba(0, 0, 0, 0.15);
    color: #666;
    font-size: 20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    border-radius: 50%;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      background: #f5f5f5;
      border-color: #2c2c2c;
      color: #2c2c2c;
      transform: rotate(90deg);
    }
  }
}

// ====================  ====================
.index-view {
  flex: 1;
  overflow-y: scroll; // 
  overflow-x: hidden;
  padding: 25px 30px 20px 30px; //  padding
  background: #fafafa;
  min-height: 0; //  flex 

  // 
  will-change: scroll-position;
  -webkit-overflow-scrolling: touch;

  //  - 
  &::-webkit-scrollbar {
    width: 12px; // 
  }

  &::-webkit-scrollbar-track {
    background: #e8e8e8; // 
    border-radius: 6px;
  }

  &::-webkit-scrollbar-thumb {
    background: #888; // 
    border-radius: 6px;
    border: 2px solid #e8e8e8; // 

    &:hover {
      background: #555; // hover 
    }
  }

  //  Firefox 
  scrollbar-width: auto; // 
  scrollbar-color: #888 #e8e8e8;
}

// 
.search-bar {
  position: relative;
  margin-bottom: 25px;

  .search-icon {
    position: absolute;
    left: 15px;
    top: 50%;
    transform: translateY(-50%);
    color: #999;
    font-size: 16px;
  }

  .search-input {
    width: 100%;
    padding: 12px 15px 12px 45px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-family: '', serif;
    font-size: 15px;
    background: #fff;
    transition: all 0.2s;

    &::placeholder {
      color: #bbb;
    }

    &:focus {
      outline: none;
      border-color: #8b4513;
      box-shadow: 0 0 0 3px rgba(139, 69, 19, 0.1);
    }
  }
}

// 
.browse-mode-selector {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 25px;
  padding: 15px;
  background: #fff;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.08);

  .selector-label {
    font-family: '', serif;
    font-size: 15px;
    font-weight: 600;
    color: #555;
  }

  .mode-select {
    flex: 1;
    padding: 10px 15px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 6px;
    font-family: '', serif;
    font-size: 14px;
    background: #fff;
    cursor: pointer;
    transition: all 0.2s;

    &:hover {
      border-color: #8b4513;
    }

    &:focus {
      outline: none;
      border-color: #8b4513;
      box-shadow: 0 0 0 3px rgba(139, 69, 19, 0.1);
    }
  }
}

// 
.category-index {
  margin-bottom: 30px;
}

// 
.letter-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(70px, 1fr));
  gap: 12px;

  .category-button {
    padding: 15px 10px;
    background: #fff;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    font-family: '', serif;

    &:hover:not(.empty) {
      border-color: #8b4513;
      background: #f8f5f0;
      transform: translateY(-2px);
      box-shadow: 0 4px 12px rgba(139, 69, 19, 0.15);
    }

    &:active:not(.empty) {
      transform: translateY(0);
    }

    &.empty {
      opacity: 0.3;
      cursor: not-allowed;
    }

    .category-label {
      font-size: 22px;
      font-weight: 700;
      color: #2c2c2c;
    }

    .category-count {
      font-size: 13px;
      color: #888;
    }
  }
}

// 
.series-list {
  display: flex;
  flex-direction: column;
  gap: 12px;

  .series-item {
    padding: 18px 20px;
    background: #fff;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 15px;
    font-family: '', serif;

    &:hover {
      border-color: #8b4513;
      background: #f8f5f0;
      transform: translateX(5px);
      box-shadow: 0 4px 12px rgba(139, 69, 19, 0.15);
    }

    .series-icon {
      font-size: 24px;
      color: #8b4513;
      min-width: 30px;
      text-align: center;
    }

    .series-info {
      flex: 1;
      display: flex;
      align-items: center;
      gap: 8px;

      .series-label {
        font-size: 16px;
        font-weight: 600;
        color: #2c2c2c;
      }

      .series-count {
        font-size: 14px;
        color: #888;
      }
    }

    .arrow-icon {
      color: #bbb;
      font-size: 14px;
    }
  }
}

// 
.quick-filters {
  padding: 20px;
  margin-bottom: 30px; // 
  background: #fff;
  border-radius: 8px;
  border: 1px solid rgba(0, 0, 0, 0.08);

  .filter-title {
    margin: 0 0 15px 0;
    font-size: 16px;
    font-weight: 600;
    color: #555;
    font-family: '', serif;
    display: flex;
    align-items: center;
    gap: 8px;

    i {
      color: #f39c12;
    }
  }

  .filter-button {
    width: 100%;
    padding: 15px 18px;
    background: #fafafa;
    border: 1px solid rgba(0, 0, 0, 0.08);
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.2s;
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 10px;
    font-family: '', serif;

    &:last-child {
      margin-bottom: 0;
    }

    &:hover {
      background: #f8f5f0;
      border-color: #8b4513;
      transform: translateX(3px);
    }

    .filter-icon {
      font-size: 18px;
      color: #8b4513;
      min-width: 24px;
      text-align: center;
    }

    .filter-label {
      flex: 1;
      font-size: 15px;
      font-weight: 500;
      color: #2c2c2c;
      text-align: left;
    }

    .filter-count {
      font-size: 14px;
      color: #888;
    }

    .arrow-icon {
      color: #bbb;
      font-size: 12px;
    }
  }
}

// ====================  ====================
.bottom-spacer {
  height: 200px; // 
  flex-shrink: 0; // 
}

// ====================  ====================
@media (max-width: 992px) {
  .monster-encyclopedia-modal {
    width: 100vw !important;
    height: 100vh !important;
    max-width: 100vw !important;
    max-height: 100vh !important;
    border-radius: 0;
    border: none;
  }

  .decoration-header .demon-decoration {
    height: 100px;
  }

  .modal-header {
    padding: 15px 20px;

    .modal-title {
      font-size: 22px;

      i {
        font-size: 26px;
      }
    }
  }

  .index-view {
    padding: 20px;
  }

  .letter-grid {
    grid-template-columns: repeat(auto-fill, minmax(60px, 1fr));
    gap: 10px;
  }
}
